a.work{
color: #f4524d; 
font-family: 'RobotoCondensed-Regular'; 
font-weight: bold;
 letter-spacing: 1px; 
 text-transform: uppercase;
 text-decoration: none;
}
a.work:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
}
.para-1{
    font-size: 14px; 
    font-family: 'Poppins-Light'; 
    max-width: 460px;
}
.head-1{
    font-family: 'RobotoCondensed-Bold'; 
    letter-spacing: 2px;
    font-size: 60px;
}

img.thumb {
    position: absolute;
    top: 0;
    Z-INDEX: 100;
    width: 600px;
    height: auto;
    left: -100px;
}
.image_1{
    position: absolute;
}
.image_1 img {
    height: auto;
    position: relative;
    top: -31px;
    right: -166%;
    width: 500px;
}
.lucky_col {
    display: grid;
    grid-template-columns: 40% 60%;
}
section.container {
    margin: 50px 0px;
    height: 100vh;
    position: relative;
}




