.touch{
    color: #f4524d; font-family: RobotoCondensed-Regular !important; font-weight: 800; letter-spacing: 1px;
    text-decoration: none;
}
a.touch:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
}
.lets_work h4{
    text-align: center; color: #f4524d; font-family: RobotoCondensed-Regular !important; letter-spacing: 1px;
}
.lets_work h2{
    text-align: center; color: #ffffff; font-family: Poppins-SemiBold !important; 
    max-width: 660px; margin: 0 auto; letter-spacing: 1px;
}
.lets_work p{
    text-align: center; color: #f4524d;
}
.lets_work {
    padding: 80px 0;
    background: black;
}
.image_3 img{
    position: relative;
    right: -165%;
    top: -113px;
    width: 500px;
}
section.container-4 {
    margin: 20px 0;
    padding: 20px 0;
    position: relative;
}
.image_3{
    position: absolute;
}


