.para-2{
    font-family: 'Poppins-Light'; font-size: 12px; max-width: 340px;
}
.head-txt {
    display: grid;
    grid-template-columns: 30% 43% 27%;
}
a.about{
    color: #f4524d; 
    font-family: 'RobotoCondensed-Regular'; 
    font-weight: bold; 
    letter-spacing: 1px;
     text-transform: uppercase;
    text-decoration: none;
}
a.about:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
}
.head-4{
    color: #f4524d; font-family: 'RobotoCondensed-Regular'; letter-spacing: 1px;
}
.head-2{
    font-family: 'Poppins-Bold'; letter-spacing: 1px;
}
.build_and_go h4{
    color: #f4524d; font-family: 'RobotoCondensed-Regular'; letter-spacing: 1px;
}
.build_and_go h2{
    font-family: 'Poppins-Bold'; letter-spacing: 1px; max-width: 480px;
}
.build_and_go p{
    font-family: 'Poppins-Light'; font-size: 12px; max-width: 340px; margin-bottom: 0px;
}
a.services{
    color: #f4524d; font-family: 'RobotoCondensed-Regular'; 
    font-weight: bold; letter-spacing: 1px; text-transform: uppercase;
    margin-top: 10px;
    display: block;
    text-decoration: none;
}
a.services:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
}
.image_2 img {
    max-width: 500px;
    position: relative;
    left: -250px;
}
.the-echoboomer {
    max-width: 700px;
}
.style-one {
    font-weight: 900;
    color: #F2F0F1;
    -webkit-text-fill-color: #F2F0F1;
}
.wordart {
    position: relative;
    display: inline-block;
    margin: 0 0 2rem;
    padding: 2rem;
}
h1.preview {
    font-size: 80px !important;
    letter-spacing: 4px;
}
.style-two{
    max-width: 800px;
    margin: 100px auto 0;
    position: relative;
}
.row{
    display: grid;
    grid-template-columns: 30% 40% 30%;
}
.row-2 {
    display: grid;
    grid-template-columns: 40% 60%;
    margin: 0 auto;
    max-width: 900px;
}
.build_and_go {
    padding-top: 100px;
    padding-left: 50px;
}
section.container-2 {
    margin: 100px 0 10px 0;
    padding: 10px 0 10px 0;
    position: relative;
}
section.style-one.style-two .wordart {
    margin: 0;
    padding: 0;
}
h1.preview.ph1 {
    margin: 10px 0 30px;
}
#container { margin: 0%;
    position: relative;
    left: -100px;
    background-image: url(/images/ebd.png);
    background-repeat: no-repeat;
    background-size: 80px;
    background-position: center 68%;
 }

#circle { position: relative; width: 100%; padding-bottom: 100%; overflow: hidden; }

#circle text { font-family: 'Helvetica Neue', Arial; font-size: 16px; font-weight: bold; }

#circle svg { position: absolute; left: 0; top: 0; width: 100%; height: 540px;

  -webkit-animation-name: rotate;
          animation-name: rotate;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;

}

@-webkit-keyframes rotate {
    from { -webkit-transform: rotate(360deg); }
    to { -webkit-transform: rotate(0); }
}
@keyframes rotate {
    from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    to { -webkit-transform: rotate(0); transform: rotate(0); }
}
#container{
max-width: 400px;
}


