.edgtf-page-header {
    display: inline-block;
    margin: 0;
    position: relative;
    vertical-align: middle;
    width: 100%;
    z-index: 110;
}
.edgtf-page-header .edgtf-menu-area {
    height: 90px;
    position: relative;
    -webkit-transition: background-color .3s ease;
    -o-transition: background-color .3s ease;
    transition: background-color .3s ease;
}
.edgtf-header-standard .edgtf-menu-area {
    color: #000;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.edgtf-page-header .edgtf-grid {
    height: 100%;
}
header .edgtf-grid:before {
    content: 'ECHO BOOMER';
    POSITION: ABSOLUTE;
    top: 25px;
    font-family: 'RobotoCondensed-Regular';
    font-size: 25px;
    left: 30px;
    font-weight: 600;
}
.edgtf-vertical-align-containers {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.edgtf-page-header .edgtf-grid .edgtf-vertical-align-containers {
    padding: 0;
    -webkit-transition: background-color .3s ease;
    -o-transition: background-color .3s ease;
    transition: background-color .3s ease;
}
.edgtf-vertical-align-containers .edgtf-position-left {
    position: relative;
    float: left;
    z-index: 2;
    height: 100%;
}

.edgtf-vertical-align-containers .edgtf-position-center-inner, .edgtf-vertical-align-containers .edgtf-position-left-inner, .edgtf-vertical-align-containers .edgtf-position-right-inner {
    vertical-align: middle;
    display: inline-block;
}
.edgtf-vertical-align-containers .edgtf-position-right {
    position: relative;
    float: right;
    z-index: 2;
    height: 100%;
    text-align: right;
}
.edgtf-header-standard .edgtf-menu-area .edgtf-vertical-align-containers .edgtf-position-right-inner {
    height: 100%;
}
.edgtf-vertical-align-containers .edgtf-position-right {
    position: relative;
    float: right;
    z-index: 2;
    height: 100%;
    text-align: right;
}
.edgtf-main-menu {
    vertical-align: middle;
    height: 100%;
}
.edgtf-logo-wrapper a, .edgtf-main-menu, .edgtf-main-menu ul li {
    position: relative;
    display: inline-block;
}
.edgtf-drop-down ul {
    list-style: none;
}
.edgtf-main-menu>ul {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    height: 100%;
}
.edgtf-main-menu ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: left;
}
.clearfix {
    clear: both;
}

.edgtf-drop-down ul li {
    position: relative;
}
.edgtf-main-menu>ul>li {
    height: 100%;
}
.edgtf-main-menu ul li {
    float: left;
    padding: 30px 30px;
    margin: 0;
    background-repeat: no-repeat;
    background-position: right;
}
.edgtf-main-menu>ul>li>a {
    height: 100%;
    position: relative;
    padding: 0;
    display: inline-block;
    vertical-align: middle;
    border-style: solid;
    border-width: 0;
    border-color: transparent;
    background-color: transparent;
    -webkit-transition: color .15s ease-out,color .15s ease-out,border-color .15s ease-out,background-color .15s ease-out;
    -o-transition: color .15s ease-out,color .15s ease-out,border-color .15s ease-out,background-color .15s ease-out;
    transition: color .15s ease-out,color .15s ease-out,border-color .15s ease-out,background-color .15s ease-out;
}
.edgtf-main-menu ul li a {
    color: #000;
    font-size: 17px;
    line-height: 21px;
    font-weight: 700;
    font-family: Montserrat,sans-serif;
    text-decoration: none;
}


