.client-word h5{
    color: #41a5fb; font-family: 'RobotoCondensed-Regular'; 
    font-weight: bold; letter-spacing: 1px; text-transform: uppercase;
    font-size: 19px;
}
.contact{
    color: #f4524d; font-family: 'RobotoCondensed-Regular'; 
    font-weight: bold; letter-spacing: 1px; text-transform: uppercase;
    text-decoration: none;
}
a.contact:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
}
.m-tag .m-top .m-tagTB{
    color:#F2F0F1;

}
.m-tag .m-middle{
    color:#000;
}
.m-tag .m-bottom .m-tagTB{
    color:#F2F0F1;
}
.m-tag ul li {
    cursor: default;
    FONT-FAMILY: 'RobotoCondensed-Bold';
    FONT-SIZE: 30PX;
    display: inline;
    padding: 0px 20px;
}
ol, ul {
    list-style-position: inside;
}
.client-word {
    text-align: center;
}
.m-tag ul {
    margin: 0px;
}
:host {
    display: inline-block;
    overflow: hidden;
    text-align: initial;
    white-space: nowrap;
}
marquee.m-tag.m-top.m-tagTB {
    color: #F2F0F1;
}
marquee.m-tag.m-bottom.m-tagTB{
    color: #F2F0F1;  
}


