
body {
    margin: 0;
 }
 .sec1{
     float: left;
 }
 .section1,.section2,.section3,.section4,.section5,.section6,.section7{
 display: inline-block;
 width:50%;
 }
 .bigImg {
    width: 100%;
    display: inline-block;
    float: left;
 }
 .smallImg {
    width: 50%;
    display: inline-block;
    float: left;
 }
 .bigImg img,.smallImg img {
    width: 100%;
 }

 .edgtf-portfolio-list-holder-outer.edgtf-ptf-gallery article .edgtf-portfolio-item-inner, .edgtf-portfolio-list-holder-outer.edgtf-ptf-masonry article .edgtf-portfolio-item-inner, .edgtf-portfolio-list-holder-outer.edgtf-ptf-pinterest article .edgtf-portfolio-item-inner {
    position: relative;
    height: 100%;
    overflow: hidden;
}
.edgtf-portfolio-list-holder-outer.edgtf-ptf-hover-sweep-left article .edgtf-item-text-overlay {
    background-color: #f4524dcc !important;
}
 .edgtf-item-text-overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    -webkit-transition: opacity .4s;
    -o-transition: opacity .4s;
    transition: opacity .4s;
}
.edgtf-portfolio-list-holder-outer.edgtf-ptf-gallery article .edgtf-item-text-overlay-inner, .edgtf-portfolio-list-holder-outer.edgtf-ptf-masonry article .edgtf-item-text-overlay-inner, .edgtf-portfolio-list-holder-outer.edgtf-ptf-pinterest article .edgtf-item-text-overlay-inner {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}
.edgtf-portfolio-list-holder-outer.edgtf-ptf-gallery article .edgtf-item-text-holder, .edgtf-portfolio-list-holder-outer.edgtf-ptf-masonry article .edgtf-item-text-holder, .edgtf-portfolio-list-holder-outer.edgtf-ptf-pinterest article .edgtf-item-text-holder {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: bottom;
    text-align: left;
    padding-left: 40px;
}
.edgtf-portfolio-list-holder-outer.edgtf-ptf-hover-sweep-left article .edgtf-item-text-holder-inner {
    text-align: left;
    position: relative;
    display: inline-block;
    width: auto;
    margin: auto;
    max-width: 70%;
}
.edgtf-portfolio-list-holder-outer.edgtf-ptf-hover-sweep-left article .edgtf-item-title, .edgtf-portfolio-list-holder-outer.edgtf-ptf-hover-sweep-left article .edgtf-ptf-category-holder, .edgtf-portfolio-list-holder-outer.edgtf-ptf-hover-sweep-left article .edgtf-separator-holder {
    position: relative;
    -webkit-transform: translate3d(40%,0,0);
    transform: translate3d(40%,0,0);
    opacity: 0;
    -webkit-transition: -webkit-transform 0s .2s,opacity .2s;
    -webkit-transition: opacity .2s,-webkit-transform 0s .2s;
    transition: opacity .2s,-webkit-transform 0s .2s;
    -o-transition: transform 0s .2s,opacity .2s;
    transition: transform 0s .2s,opacity .2s;
    transition: transform 0s .2s,opacity .2s,-webkit-transform 0s .2s;
}
h2.edgtf-item-title {
    color: white;
    font-weight: 600 !important;
    font-size: 20px !important;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.edgtf-portfolio-list-holder article .edgtf-ptf-category-holder {
    color: #fff;
    font-size: 14px;
    font-weight: 200;
    font-style: normal;
}
.ow-1{
    text-align: center;
}
.ow{
    margin:0;
    padding: 0;
}
.ow h1{
    margin: 0;
    font-size: 100px !important;
}
.assets-img{
    width: 100px;
}


