.learn{
    color: #f4524d; font-family: 'RobotoCondensed-Regular'; 
    font-weight: bold; letter-spacing: 1px; text-transform: uppercase;
    text-decoration: none;
}
.col-2 p{
    font-family: 'Poppins-Light'; font-size: 12px;
    max-width: 455px;
    padding: 0 20px 0 0;
}
.headt-h2{
    font-family: 'Poppins-Bold' !important; letter-spacing: 1px;
}
.headt-h4{
    color: #f4524d; 
    font-family: 'RobotoCondensed-Regular' !important; letter-spacing: 1px;
}
.preview_2 {
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-transform: rotate( -180deg);
        -ms-transform: rotate( -180deg);
            transform: rotate( -180deg);
}
.row-t {
    display: grid;
    grid-template-columns: 30% 70%;
}
.sub-row {
    display: grid;
    grid-template-columns: auto auto auto;
}
.main-img img {
    width: 300px;
}
section.team-sec {
    padding: 50px 0;
}
.para-row{
    display:  -ms-flexbox;
    display:  flex;
}
a.learn:after {
    content: "➜";
    font-size: 20px;
    font-weight: 900;
    position: relative;
    top: 2px;
    left: 5px;
    text-decoration: none;
}


