footer .edgtf-footer-top:not(.edgtf-footer-top-full) .edgtf-container-inner {
    padding: 60px 0 30px !important;
}
.its_footer {
    display: block;
}
.edgtf-container-inner{
    width: 1100px;
}
.its_first_inner.desktop {
    display: grid;
    
    grid-template-columns: auto auto auto auto auto;
    color: #fff;
    grid-column-gap: 6%;

}

.its_col1 {
    display: grid;
    grid-template-columns: auto auto;
}
.its_col1 img {
    width: 50px;
    position: relative;
    top: -26px;
    margin-right: 10px;
}
.its_col1 span {
    font-size: 20px;
    display: block;
    font-family: 'RobotoCondensed-Bold';
}
.its_col2 span, .its_col3 a, .its_col4 a {
    display: block;
    color: #fff;
    font-size: 10px;
    font-family: 'Poppins-Light';
    line-height: 25px;

    letter-spacing: 1px;
    text-decoration: none;
}
.its_col5 {
    font-size: 10px;
    line-height: 16px;
    font-family: 'Poppins-Light';
    letter-spacing: 1px;

}
.its_second_inner {
    text-align: center;
    color: #fff;
    font-size: 10px;
    margin-top: 30px;
    font-family: 'Poppins-Light';
    letter-spacing: 01px;
}
footer .abc {
    display: block;
    background-color: #1b1a18;
    background-position: center center;
    background-size: cover;
    padding: 80px;
}


