.slider-news{
    width: 100%; display: inline-block;
}
.our_news .slick-slide.slick-active .col-1-.mason-item {
    height: 200px !important;
    background: #fff !important;
}
.col-1.mason-item {
    display: -ms-flexbox;
    display: flex;
}
.our_news .slick-slide.slick-active .mega-post-carousel4 {
    margin: 0px !important;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
}
.mega-post-carousel4 {
    background: #fff;
    padding: 0px 20px;
}
body .our_news .mega-post-carousel4 .mega-post-category {
    margin-top: 20px !important;
    margin-left: -4px !important;
}
.our_news .mega-post-carousel4 .mega-post-category {
    margin: 0px !important;
    position: relative;
}
.mega-post-carousel4 .mega-post-category {
    margin: 10px 0;
    display: inline-block;
}
.mega-post-category {
    float: left;
}
.mega-post-carousel4 .mega-post-category a {
    text-decoration: none;
    display: inherit;
    color: #fff;
    background: #000;
    font-size: 11px;
    padding: 2px 5px;
    margin-right: 2px;
   
}
.mega-post-carousel4 .mega-post-title a{

    float: left;
}
.mega-post-carousel4 .mega-post-category a {
    background: #fff !important;
    color: #41a5fb !important;
    font-size: 16px !important;
    text-transform: uppercase;
    font-family: 'RobotoCondensed-Regular';
}
.our_news .slick-slide.slick-active .col-1-.mason-item .mega-post-category a::before {
    content: "➜";
    position: absolute;
    bottom: -139px;
    left: 279px;
    color: #E44D48;
    font-size: 20px;
}
.mega-post-category a::after {
    content: " |";
}
span.mega-post-date {
    float: left !important;
    color: #41a5fb !important;
    font-size: 10px !important;
    font-family: 'POPPINS-THIN';
    font-weight: 600;
    margin-top: 20px !important;
}
.our_news span.mega-post-date {
    padding: 0px !important;
    margin-left: -10px;
}
.our_news h3.mega-post-title {
    display: block !important;
    line-height: 20px !important;
    width: 100% !important;
    float: left;
    margin-top: 0px;
}
h3.mega-post-title {
    font-family: 'Poppins-Bold';
    letter-spacing: 0.5px;
}
.mega-post-carousel4 .mega-post-title {
    margin-bottom: 10px;
}
.our_news .mega-post-title a {
    font-size: 16px !important;
}
.our_news .mega-post-para p {
    line-height: 16px !important;
    margin: 0px !important;
}
.mega-post-para p {
    font-family: 'Poppins-Light';
    font-size: 12px !important;
    color: black !important;
    letter-spacing: 0.5px;
}
.clearfix {
    clear: both;
}
.our-news .slick-slider {
    position: relative;
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.our-news .slider {
    margin: 0px auto;
}
.our_news .slick-list.draggable {
    height: 350px !important;
}
.our-news .slick-slider .slick-track, .slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}
.our-news .slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
}
.our-news .slick-track {
    position: relative;
    top: 0;
    left: 0;
}
.our-news .slick-track:before, .slick-track:after {
    display: table;
    content: '';
}
.our-news .slick-initialized .slick-slide {
    display: block;
}
.our-news .slick-slide {
    padding: 0 15px 0 20px !important;
}
.our-news .slick-track {
    display: -ms-flexbox;
    display: flex;
}
.our-news .slick-prev:before {
    content: "➜" !important;
    color: #4AA2FC !important;
}
.our-news .slick-next:before {
    content: "➜" !important;
    color: #fff !important;
}

.our-news .slick-next:before {
    background: #4AA2FC !important;
    border: 1px solid #4AA2FC;
}
.our-news .slick-next:before {
    border-radius: 50%;
    padding: 11px 10px !important;
    font-size: 20px;
}
.our-news .slick-prev:before {
    background: transparent !important;
    border: 1px solid #4AA2FC;
    font-size: 20px;
}
.our-news .slick-prev:before{
    border-radius: 50%;
    padding: 11px 10px !important;
}
.our-news .slick-prev.slick-arrow {
    left: 40px !important;
    top: 244px;
    -webkit-transform: rotate(
180deg
);
        -ms-transform: rotate(
180deg
);
            transform: rotate(
180deg
);
}
.our-news button.slick-arrow.slick-next {
    left: 100px;
    top: 252px;
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
    color: transparent;
    outline: none;
    /* border: none; */
    position: absolute;
    border: 0px !important;
    background: none !important;
}
.our-news button.slick-arrow.slick-prev {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
    color: transparent;
    outline: none;
    /* border: none; */
    position: absolute;
    border: 0px !important;
    background: none !important;
}
.mega-post-carousel4 .mega-post-title a {
    text-decoration: none;
    color: #000;
    line-height: 1.2;
    font-size: 19px;
}
.our-news{
    background-color: #F2F0F1;
    padding: 50px 0 130px;
}
.news{
    color: #f4524d;
    font-family: 'RobotoCondensed-Bold';
    padding: 20px;
}


